 
import VueRouter from 'vue-router'
 
// 引入一下用到的组件 
import ImgList from '../components/ImgList.vue'
import Drag from '../components/Drag.vue'
export default new VueRouter({
    routes: [
        {
            // path是路径
            path: "/ImgList",
            //跳转的组件
            component: ImgList
        },    
        {
            // path是路径
            path: "/Drag",
            //跳转的组件
            component: Drag
        },    
    ]
})