<template>
  <div class="page">
    <div class="bgimg_box" id="bgimg_box">
        <div id="bgimg" class="bgimg"></div>
        <div class="demo" id="demo">
            <!-- <img id="demoImg" :src=imgurl class="demo-img" /> -->

        </div>
    </div>
    <div>
        <div class="btn-wrap">
            <div class="upload">
                <input type="file"
                accept="image/jpeg, image/png, image/jpg"
                @change="changeImage($event)"
                class="input-opacity"
                id="upload"
                >
                <label class="input-file-button" for="upload">上传图片</label>
            </div>
            <div class="compose hide">
                <van-button type="info"  @click="picCompose()">合成图片</van-button>
            </div>
            <div class="show-compose hide">
                <van-button type="info"  @click="showPicCompose()">预览图片</van-button>
            </div>
        </div>
        
        <div class="tz-box hide">
            <img :src="require('../assets/tz/t1.png')" alt="" class="tzimg" @click="tzClick($event)">
            <img :src="require('../assets/tz/t2.png')" alt="" class="tzimg" @click="tzClick($event)">
            <img :src="require('../assets/tz/t3.png')" alt="" class="tzimg" @click="tzClick($event)">
            <img :src="require('../assets/tz/t4.png')" alt="" class="tzimg" @click="tzClick($event)">
        </div>
    </div>
    <router-link class="list-group-item" active-class="active" to="/ImgList">
      <van-button type="info"  >图片列表</van-button>
    </router-link>
    

 
  </div>
</template>
<script>
import Hammer from 'hammerjs'
import { setTimeout } from 'timers'
import html2canvas from 'html2canvas';
import t1 from '../assets/tz/t1.png';
import { Dialog,Toast,ImagePreview  } from 'vant';


export default {
    name: 'Drag',
    data () {
    return {
      imgurl:'',
      imgArr: [],
      picComposeUrl:'',

      config: {},
      id: null,
      mc: null,
      timer: false,
      translateX: 0,
      translateY: 0,
      scale: 1,
      firstTouch: true,
      relateX: 0,
      relateY: 0,
      oldX: 0,
      oldY: 0,
      oldScale: 1
    }
  },
  mounted () {
    this.$nextTick(() => {
    //   this.picInit()
    })
  },
  methods: {
    //上传图片点击
    changeImage (e) {
        let _this = this;
        const fileObj = new FileReader()
        fileObj.readAsDataURL(e.target.files[0])
        fileObj.onload = () => {
        this.picReduce(fileObj.result, img => {
            this.imgArr.push(img)
            document.getElementsByClassName("upload")[0].classList.add("hide");
            this.removeClasss(document.querySelectorAll(".compose"),"hide");
            this.removeClasss(document.querySelectorAll(".tz-box"),"hide");
        //   _this.picInit()
        })
      }
    },
    //加载上传的图片并放置
    picReduce (picObj,callback) {
      const img = new Image()
      img.src = picObj
      img.onload = () => {
        img.className = "demo-img"
        img.id = "demoImg"
        if(document.getElementById("demoImg")){
            document.getElementById("bgimg").removeChild(document.getElementById("demoImg"))
        }
        document.getElementById("bgimg").appendChild(img);
    
        callback(img)
      }
    },

    //贴图点击
    tzClick(el){
        let _this = this;
        console.log(el);
        this.removeClasss(document.querySelectorAll(".tzimg"),"on");
        el.target.classList.add('on');
        let imgurl = el.target.currentSrc;
        console.log(el.target.currentSrc);
        const img = new Image()
        img.src = imgurl;
        img.onload = () => {
            img.className = "tz-img"
            img.id = "tzImg"
            if(document.getElementById("tzImg")){
                document.getElementById("bgimg").removeChild(document.getElementById("tzImg"))
            }
            document.getElementById("bgimg").appendChild(img);
            _this.picInit();
      }
    },
    //删除Class名
    removeClasss(eleList,txt){
        for(var i=0;i<eleList.length;i++){
            let ele = eleList[i];
            console.log(ele);
            var str =  ele.className,
            index = str.indexOf(txt);
            if(index > -1){
                ele.className = str.replace(txt,"");
            }
        }
    },

    //贴图初始化
    picInit () {
      this.id = document.getElementById('tzImg')
      this.mc = new Hammer(this.id)
    //   this.relateX = (document.getElementById("bgimg_box").clientWidth - this.id.offsetWidth) / 2
    //   this.relateY = (document.getElementById("bgimg_box").clientHeight - this.id.offsetHeight) / 2
      this.relateX=0;
      this.relateY=0;

      this.mc.add(new Hammer.Pan({
        direction: Hammer.DIRECTION_ALL,
        threshold: 0,
        pointers: 0
      }))
      this.mc.add(new Hammer.Pinch({
        threshold: 0
      })).recognizeWith(this.mc.get('pan'))

      this.mc.on('hammer.input', this.isFinal)
      this.mc.on('panstart panmove', this.onPan)
      this.mc.on('pinchstart pinchmove', this.onPinch)
      this.setPosition()
    },
    isFinal (ev) {
      if (ev.isFinal) {
        this.oldX = this.translateX
        this.oldY = this.translateY
        this.oldScale = this.scale
      }
    },
    // 初始化图片位置及缩放
    setPosition () {
      this.selfPosition({
        translateX: this.relateX,
        translateY: this.relateY,
        scale: this.scale
      })
    },
    // 单点触发 - 拖拉
    onPan (ev) {
      // console.log(this.firstTouch)
      if (this.firstTouch) {
        this.oldX = this.relateX
        this.oldY = this.relateY
      }
      // console.log(this.oldX)
      // console.log(this.oldY)
      this.translateX = this.oldX + ev.deltaX
      this.translateY = this.oldY + ev.deltaY
      console.log(this.translateX,this.translateY)
     
      this.translateOr();

      const position = {
        translateX: this.translateX,
        translateY: this.translateY,
        scale: this.scale
      }
      this.selfPosition(position)
      this.firstTouch = false
    },
    //设置边界值
    translateOr(){
        let bgImgWidth = document.getElementById("demoImg").clientWidth;
        let bgImgHeight = document.getElementById("demoImg").clientHeight;
        let tzImgWidth = document.getElementById("tzImg").clientWidth;
        let flNum = this.scale * tzImgWidth / 2;
        if(this.translateX < -flNum){
            this.translateX = -flNum ;
        }
        if(this.translateY < -flNum ){
            this.translateY = -flNum;
        }
        if(this.translateX > bgImgWidth - flNum){
            this.translateX  =  bgImgWidth - flNum;
        }
        if(this.translateY > bgImgHeight - flNum){
            this.translateY = bgImgHeight - flNum;
        }
    },
    // 多点触发 - 缩放
    onPinch (ev) {
      this.scale = this.oldScale * ev.scale
      this.translateOr();
      this.selfPosition({
        translateX: this.translateX,
        translateY: this.translateY,
        scale: this.scale
      })
      // this.selfPosition(this.position)
    },
    selfPosition (pos) {
      return this.picAnimate()(() => this.tempPos(pos))
    },
    tempPos (pos) {
      let style = [
        `translate3d(${pos.translateX}px, ${pos.translateY}px, 0)`,
        `scale(${pos.scale}, ${pos.scale})`
        // `scale(${pos.scale > 1.2 ? 1.2 : pos.scale}, ${pos.scale > 1.2 ? 1.2 : pos.scale})`
      ]
      style = style.join(' ')
      this.id.style.transform = style
    },
    picAnimate () {
      return window[Hammer.prefixed(window, 'requestAnimationFrame')] || function (callback) {
        setTimeout(callback, 1000 / 60)
      }
    },
    //合成图片
    picCompose(){
        let _this = this;
        function beforeClose(action, done) {
            if (action === 'confirm') {
                html2canvas(document.getElementById("bgimg")).then(function(canvas) {
                    // document.body.appendChild(canvas);
                    _this.picComposeUrl = canvas.toDataURL("image/png");
                    Toast.success('合成成功');
                    _this.removeClasss(document.querySelectorAll(".show-compose"),'hide')
                    done();
                });
            } else {
                done();
            }
        }
        Dialog.confirm({
            // title: '标题',
            message: '是否合成图片？',
            beforeClose,
        });
    },
    //预览合成图
    showPicCompose(){
        let src = this.picComposeUrl
        ImagePreview([
            src
        ]);
    }

  }
}
</script>
<style >
.hide{
  display: none;  
}
.btn-wrap{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.bgimg_box{
    display: block;
    overflow: hidden;
    width: 100%;
    min-height: calc(100vh - 300px);
    display: flex;
    justify-content: center;
    align-items: center;
}
.bgimg{
    position: relative;
    overflow: hidden;
}
.tz-box{
    width:100%;
    height: 220px;
    overflow-y:scroll;
    text-align: left;
}
.tz-box::-webkit-scrollbar {
    display:none
}
.tzimg{
    width: 25%;
    box-sizing: border-box;
}
.tzimg.on{
    border: 1px solid #e5e5e5;
}
.tz-img{
    width:100px;
    height: 100px;
    position: absolute;
    top:0;
    left: 0;;
}
.demo {
  position: absolute;
}
.demo-img {
  max-width: 100%;
  max-height: calc(100vh - 300px);
  display: block;
  overflow: hidden;
  margin:0 auto;
}
.input-file-button{
    display: inline-block;
    height: 40px;
    padding: 10px 15px;
    border-radius: 5px;
    box-sizing: border-box;
    color: #FFF;
    background-color: #409EFF;
    cursor: pointer;
    text-align: center;
    font-size: 14px;
}
.input-opacity{
    display: none;
}

</style>
